    <pre><code>&lt;button id=&#39;X&#39; onclick=&quot;print&quot;&gt;&lt;/button&gt;
&lt;button id=&#39;Y&#39; onclick=&quot;print()&quot;&gt;&lt;/button&gt; // 
&lt;button id=&#39;Z&#39; onclick=&quot;print.call&quot;&gt;&lt;/button&gt; // 
