&lt;script&gt;
    function print() {
        console.log(&#39;hi&#39;)
    }

    X.onclick = print  // JS X.onclick = function () {} print
    Y.onclick = print()
    Z.onclick = print.call()
&lt;/script&gt;
</code></pre><h4 id="dom-level-2">Dom Level 2</h4>
