<template>
  <div class="wrap">
    <h3>/</h3>
    <div class="demo">
      <div id="demo">
        <button v-on:click="show = !show">
          Toggle
        </button>
        <transition name="fade">
          <p v-if="show">hello</p>
        </transition>
      </div>
    </div>
    <div class="demo">
      <h3>#css</h3>
      <div class="css-transition-demo">
        <button @click="change = !change">
          Change
        </button>
        <transition name="slide-fade">
          <p v-if="change">
            
          </p>
        </transition>
      </div>
    </div>
    <div class="demo">
      <h3>#css</h3>
      <div class="css-animation-demo">
        <button @click="animation = !animation">
          Animation
        </button>
        <transition name="bounce">
          <p v-if="animation">
            
          </p>
        </transition>
      </div>
    </div>
    <div id="demo">
      <button @click="show = !show">
        Toggle
      </button>
      <transition
        v-on:before-enter="beforeEnter"
        v-on:enter="enter"
        v-on:leave="leave"
        v-bind:css="false"
      >
        <p v-if="show">
          Demo
        </p>
      </transition>
    </div>
  </div>
</template>

<script>
  export default {
    name: "animation.vue",
    data() {
      return {
        show: true,
        change: true,
        animation: true
      }
    }
  }
</script>

<style scoped>
  .demo {
    border: 1px solid #ddd;
    width: 400px;
    min-height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 20px 0;
  }

  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
  }

  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }

  .slide-fade-enter, .slide-fade-leave-to {
    transform: translateX(10px);
    opacity: 0;
  }

  .slide-fade-enter-active, .slide-fade-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }

  .bounce-enter-active {
    animation: bounce-in .5s;
  }

  .bounce-leave-active {
    animation: bounce-in .5s reverse;
  }

  @keyframes bounce-in {
    0% {
      transform: scale(0);
    }
    50% {
      transform: scale(1.5);
    }
    100% {
      transform: scale(1);
    }
  }
</style>



